body{margin:0;padding:0;background-color:#fbfbfb;font-family:"Fira Sans", sans-serif}.Section{display:flex;position:fixed;width:calc(100vw - 100px);height:100vh;z-index:123;top:0;left:100px;overflow:hidden}@media (max-width: 1024px){.Section{position:relative;height:inherit}}@media (max-width: 600px){.Section{left:0;width:100%}}.Section.Section2{justify-content:flex-start;align-items:center}@media (max-width: 1024px){.Section.Section2{background-color:#fbfbfb}}.Section.Section2 .Section2Inner{width:100%;height:80%;display:flex;align-items:center;margin:0 10vw}@media (max-width: 1024px){.Section.Section2 .Section2Inner{flex-direction:column;height:inherit}}@media (max-width: 600px){.Section.Section2 .Section2Inner{margin:0}}.Section.Section2 .Section2Right{width:100%;height:100%;display:flex;align-items:center;justify-content:center}@media (max-width: 1024px){.Section.Section2 .Section2Right{margin-top:50px;margin-bottom:70px;height:auto;height:initial}}.Section.Section2 .Section2RightInner{width:77%}.Section.Section2 .OpenSourceSection,.Section.Section2 .SkillsSection{margin-top:65px}@media (max-width: 1440px){.Section.Section2 .OpenSourceSection,.Section.Section2 .SkillsSection{margin-top:48px}}.Section.Section2 .OpenSourceSectionProjects{display:flex;justify-content:space-between}@media (max-width: 1024px){.Section.Section2 .OpenSourceSectionProjects{flex-direction:column}}.ImagesHolder{position:relative;width:600px;height:600px;max-width:80%;max-height:65%}@media (max-width: 600px){.ImagesHolder{max-width:125%;max-height:71%}}.SkillItem{margin-right:8px;padding:0 3px;display:inline-block;font-family:monospace}.Side{display:flex;width:50vw;flex-direction:column;justify-content:center;height:100vh}.Side.LeftSide{justify-content:flex-start;width:40%;position:fixed;z-index:12;height:200vh}@media (max-width: 1024px){.Side.LeftSide{width:100%;position:relative}}.Side.RightSide{left:50%;position:absolute;background:radial-gradient(circle, rgba(0,0,0,0.13) 12%, rgba(255,255,255,0) 72%)}@media (max-width: 1024px){.Side.RightSide{position:fixed;left:34%;opacity:0.3;background:none}}@media (max-width: 600px){.Side.RightSide{left:18%}}.FakeScroll{position:relative}.ButtonsHolder{margin-top:28px}.react-github-calendar{font-family:monospace}

.Link a{display:block;text-decoration:none;color:#000}.TextLink{border-bottom:1px solid #ececec;height:25%;display:flex;align-items:center}.TextLink a{display:inline-block;font-size:9vh;color:currentColor;letter-spacing:0.01em;padding-left:10vw}@media (max-width: 600px){.TextLink a{font-size:6vh}}.ImageLink{position:absolute;top:0;left:0;width:100%;height:100%;box-shadow:4px -4px 20px 0px rgba(0,0,0,0.03);-webkit-backface-visibility:hidden;backface-visibility:hidden}@media (max-width: 1024px){.ImageLink{box-shadow:4px -4px 20px 0px rgba(0,0,0,0.13)}}.ImageLink a{border:2px solid #ffffff;line-height:0;width:100%;height:100%}@media (max-width: 1024px){.ImageLink:nth-child(1){z-index:9}.ImageLink:nth-child(2){z-index:8}.ImageLink:nth-child(3){z-index:7}.ImageLink:nth-child(4){z-index:6}.ImageLink:nth-child(5){z-index:5}.ImageLink:nth-child(6){z-index:4}.ImageLink:nth-child(7){z-index:3}.ImageLink:nth-child(8){z-index:2}}.ImageLink:nth-child(1){margin-top:28px;margin-left:10px;transform:rotate(-4deg)}.ImageLink:nth-child(2){margin-top:20px;margin-left:20px;transform:rotate(-2deg)}.ImageLink:nth-child(3){transform:rotate(5deg)}.ImageLink:nth-child(6){transform:rotate(-2deg)}.ImageLink:nth-child(7){transform:rotate(3deg)}.ImageLink img{width:100%;height:100%;object-fit:cover;box-sizing:border-box;border-bottom:130px solid #fff}.ImageLink .ImageDescription{font-family:"Long Cang", cursive;position:absolute;bottom:6%;font-size:44px;width:100%;text-align:right;padding:0 8%;box-sizing:border-box}@media (max-width: 600px){.ImageLink .ImageDescription{bottom:12%;font-size:25px}}

.ProfileCard{width:430px;padding:50px;background:#fff;position:relative;max-height:100%;max-width:100%;box-sizing:border-box}@media (max-width: 1440px){.ProfileCard{width:456px}.ProfileCard h1{font-size:25px}}@media (max-width: 1024px){.ProfileCard{margin-top:70px}}@media (max-width: 600px){.ProfileCard{margin-top:0}}.ProfileCard img{width:320px;border-radius:100%;border:1px solid #ececec;padding:20px;box-sizing:border-box}@media (max-width: 1440px){.ProfileCard img{width:81%;padding:5%}}.ProfileCardShadow{position:absolute;width:150%;height:100%;top:0;left:-28%;background:radial-gradient(circle, rgba(0,0,0,0.03) 12%, rgba(255,255,255,0) 72%);z-index:-1}.ProfileInfoTopHolder{margin:20px 0}.ProfileInfoTopHolder p{margin:0;padding:0}@media (max-width: 1440px){.ProfileInfoTopHolder p{font-size:13px;white-space:nowrap}}.ProfileInfoTopHolder .ProfileInfoLocation{margin-top:5px;font-size:14px}.ProfileImageHolder{text-align:center;margin-bottom:25px}.ProfileInfoDescription p{font-size:14px;color:#5f5f5f}@media (max-width: 1440px){.ProfileInfoDescription p{font-size:13px}}

.SocialIcon{display:inline-block;margin:0 5px}

.Button{display:inline-block;color:#d85936;border:1px solid currentColor}.Button:nth-child(1){margin-right:15px;margin-bottom:15px}@media (max-width: 1440px){.Button:nth-child(1){margin-right:4px;margin-bottom:10px}}.Button a{display:block;text-decoration:none;color:inherit;padding:15px 40px;font-family:sans-serif;font-size:13px;letter-spacing:0.03em}@media (max-width: 1440px){.Button a{padding:10px 22px}}

.Header{position:fixed;height:100vh;display:flex;justify-content:center;align-items:center;width:100px;border-right:1px solid #ececec;z-index:1234;background:#fff;top:0;left:0;overflow:hidden}.Header .HeaderTitleHolder{font-family:"Kanit", sans-serif;font-size:42px;text-transform:uppercase;-webkit-text-stroke:1px #9e9e9e;-webkit-text-fill-color:transparent;transform:rotate(-90deg);letter-spacing:0.04em;white-space:nowrap;line-height:2em}.Header .HeaderTitle{width:100%;height:100%;top:0;left:0;margin:0;padding:0;text-align:center}.Header .HeaderTitle:nth-child(2){position:absolute}.Header .HeaderSmallText{font-family:monospace;text-transform:uppercase;transform:rotate(-90.5deg);letter-spacing:0.04em;position:absolute;bottom:8%;white-space:nowrap;font-size:12px}

.Logo{width:100%;height:44px;position:absolute;top:0;left:0;text-align:center;padding-top:22px}.Logo img{border-radius:100%;height:100%;padding:6px;border:1px solid #ececec}

.ProjectCard{padding:25px;background:#ffffff;border:1px solid #cecece;width:31%;box-sizing:border-box;display:inline-block;display:flex;flex-direction:column}@media (max-width: 1024px){.ProjectCard{width:80%}.ProjectCard:not(:last-child){margin-bottom:20px}}.ProjectCard .ProjectCardTitle{margin-bottom:10px;text-decoration:none;color:#000}.ProjectCard .ProjectCardTitle svg{position:relative;display:inline-block;top:2px;margin-right:5px}.ProjectCard .ProjectCardDescription{font-size:12px;line-height:1.5em;color:#5f5f5f}.ProjectCard .ProjectCardTech{margin-top:30px;font-family:monospace;flex-grow:1;display:flex;align-items:flex-end}.ProjectCard .ProjectCardTech span{margin-right:8px;padding:3px 4px;border:1px solid #cecece}

